import React, { useState, useEffect} from 'react'
import { NavLink } from 'react-router-dom';
import styled from 'styled-components'
import toast from 'react-hot-toast'
// import '../styles/contacts.css'


export default function Contacts({contacts,currentUser, changeChat}) { 

  const[currentUserName, setCurrentUserName] = useState(undefined);
  const[currentUserImage, setCurrentUserImage] = useState(undefined);
  const[currentSelected, setCurrentSelected] = useState(undefined);

  const handlelogout = () => {
    localStorage.removeItem('auth')
    toast.success('Logout Successfully')
  }

  useEffect(() => {
        if (currentUser) {
          setCurrentUserImage(currentUser.avaterImage)
          setCurrentUserName(currentUser.user.name)
        }
    // eslint-disable-next-line
  },[currentUser]);

    const changeCurrentChat = (index, contact) => {
      setCurrentSelected(index)
      changeChat(contact)
    };
  return (
    <>
        {currentUserImage && currentUserName && (
          <Container>
              <div className='brand'>
                <h3>CHATWAVE</h3>
              </div>
              <div className='contacts'>
                {
                  contacts.map((contact,index) => {
                    return(
                      <div className='contact' >
                      <div className='{contact `${index === currentSelected ? "selected" : ""}`}'
                      key={index} onClick={() => changeCurrentChat(index, contact)}
                       > 
                       <div className="avatar">
                        <img src={`data:image/svg+xml;base64,${contact.avatarImage}`} alt="avatarrrr" />
                        </div>
                       <div className="username">
                        <h5>{contact.name}</h5>
                       </div>
                      </div>
                      </div>
                    )
                  })
                }
              </div>
              <div className="current-user">
              <div className="avatar">
                        <img src={`data:image/svg+xml;base64,${currentUserImage}`} alt="avatarr" />
                       </div>
                       <div className="username">
                        <h2>{currentUserName}</h2>
                        <NavLink onClick={handlelogout} to="/login" className="dropdown-item " >Logout</NavLink>
                       </div>
              </div>
            </Container>
          ) }
    </>
  )
}
const Container = styled.div`
display: grid;
grid-template-rows: 10% 75% 15%;
 overflow: hidden;
 background-color: #30305f; 
height: 85vh;
width: 25vw;
.brand{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  h3{
      color: white;  
  }
} 
.contacts{
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  gap: 0.8rem;
  &::-webkit-scrollbar{
    width: 0.2rem;
    &-thumb{
        background-color: #ffffff39;
        width: 0.1rem;
    }
   }
  .contact{
    background-color: #bab5b5;
    min-height: 5rem;
    width: 90%;
    cursor: pointer;
       border-radius: 0.2rem;
       padding: 0.4rem;
       gap: 1rem;
       align-items: center;
       display: flex;
       transition: 0.5s ease-in-out;
    .avatar{
      img{
        height: 3rem;
      }
    }
    .username{
      h5{ 
        color: white; 
      }
    }
    }
    .selected{
      background-color: #9186f3;
    }
  }
  .current-user{
    background-color: #0d0d30;
       display: flex;
       justify-content: center;
       align-items: center;
       gap: 2rem;
       .avatar{
        img{
            height: 4rem;
            max-inline-size: 100%;
        }
       }
       .username{
        h2{
          color: white; 
        }
        .dropdown-item{
          color: white;
        }
       }
  }
}
`;

