import React,{useState,useEffect} from 'react'
import toast, { Toaster } from 'react-hot-toast'
import axios from 'axios';
import {useNavigate} from 'react-router-dom';
import styled from 'styled-components'
import {Buffer} from 'buffer'
import '../styles/setAvtar.css'; 
import {Spinner} from 'react-bootstrap'

export default function SetAvatar() {

  const api = "https://api.multiavatar.com/45678945";
  const navigate = useNavigate();
  const [avatars,setAvatars] = useState([]);
  const [isloading,setIsLoading] = useState(true);
  const [selectedAvatar,setSelectedAvatar] = useState(undefined);

  useEffect(() => {
    if (!localStorage.getItem("auth")) {
      navigate('/login');
    }
    // eslint-disable-next-line
  },[])

  const setProfilePicture = async () => {
    if (selectedAvatar === undefined) {
      toast.error("please select an avatar")
    } else{
       
      const user = await JSON.parse(localStorage.getItem("auth"));
      const {data} = await axios.post(`/api/v1/Avatar/setAvatar/${user.user._id}`, {image: avatars[selectedAvatar]
      });

      if(data.isSet){
        user.isAvatarImageSet = true;
        user.avaterImage = data.image;
        localStorage.setItem("auth", JSON.stringify(user));
        navigate("/");
      }else{
        toast.error("Error setting avatar. please try again")
      }
    }
  };

  const doSomething = async() =>{
    const data =[];
    for (let i = 0;  i < 4; i++) {
        const image = await axios.get(
            `${api}/${Math.round(Math.random()*1000)}`
        );
        const buffer = new Buffer(image.data);
        data.push(buffer.toString("base64"));
    }
    setAvatars(data);
    setIsLoading(false);
    }

  useEffect( () => {
    doSomething();
  },[]);


  return (
    <>
     {
      isloading ? <Spinner animation="border" variant="warning" /> : (<>
        
           <Toaster/>
         <div className='container'>
            <div className='title-container'>
                <h1>pick an avatar as your profile pictures</h1>
            </div>
            <div className='avatars'>
                {avatars.map((avatar,index) => {
                    return(
            
                        <div key={index} className={`avatar ${
                            selectedAvatar === index ? "selected" : ""
                        }`}>
                            <img src={`data:image/svg+xml;base64,${avatar}`} alt="avatar" 
                            onClick={() => setSelectedAvatar(index)} />
                        </div>
                    )
                })}
            </div>
                     <button className='submit-btn' onClick={setProfilePicture}>SET AS PROFILE PICTURE</button>
         </div>

         </>   )
     }

    </>
  )
}

