import React,{useState,useEffect} from 'react'
import toast, { Toaster } from 'react-hot-toast'
import axios from 'axios';
import {useNavigate} from 'react-router-dom';
import '../styles/AuthStyle.css'; 

const Register = () => {
    const [name,setName] = useState("")
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const [phone,setPhone] = useState("")
    const [answer,setAnswer] = useState("")
    const navigate = useNavigate()


    useEffect(() => {
      if (localStorage.getItem("auth")) {
        navigate('/');
      }
      // eslint-disable-next-line
    },[])

    //form function
    const handlesubmit = async (e) => {
      e.preventDefault();
      try {
        const res = await axios.post('/api/v1/auth/register',{name,email,password,phone,answer});
        if (res && res.data.success) {
          toast.success(res.data && res.data.message);
          navigate('/login');
        } else {
          toast.error(res.data.message);
        };
      } catch (error) {
        console.log(error);
        toast.error('Something went wrong')
      }
    }

  return (
    <div >
       <Toaster/>
        <div className='form-container'>
            <form onSubmit={handlesubmit} >
            <h4 className='title'><img src="/images/ssk.jpeg" alt="sss" className='imgs' />ChatWave</h4>
              <div className="mb-3">
                <input type="text" value={name} className="form-control" id="exampleInputEmail1" 
                placeholder='Enter Your Name'
                 onChange={(e) => setName(e.target.value)} required />
              </div>
               <div className="mb-3">
                <input type="email" value={email} className="form-control" id="exampleInputEmail1"
                 placeholder='Enter Your Email'
                  onChange={(e) => setEmail(e.target.value)} required />
              </div>
              <div className="mb-3">
                <input type="password" value={password} className="form-control" id="exampleInputPassword1"
                 placeholder='Enter Your Password'
                  onChange={(e) => setPassword(e.target.value)} required />
              </div>
              
               <div className="mb-3">
                <input type="text" value={phone} className="form-control" id="exampleInputEmail1"
                 placeholder='Enter Your Phone'
                  onChange={(e) => setPhone(e.target.value)} required/>
              </div>
              
              <div className="mb-3">
                <input type="text" value={answer} className="form-control" id="exampleInputEmail1"
                 placeholder='What is your fav sports'
                  onChange={(e) => setAnswer(e.target.value)} required/>
              </div>
        
              <button type="submit" className="btn btn-primary">REGISTER</button>
              
            </form>
            <div className='bottomlogin'>AALREADY HAVE AN ACCOUNT ? <a href='/login'>login</a></div>
        </div>
         
    </div>
  )
}

export default Register

